<template>
  <div class="todo">
    <h1>Todo App</h1>
    <input
      @keyup.enter="add"
      v-model="title"
      type="text"
      placeholder="what you want to do"
    />
    <button @click="add">Add to list</button>

    <ul>
      <li v-for="(todo, index) of todos" :key="index">
        {{ todo }}<span><button @click="remove(index)">Remove</button></span>
      </li>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "todo",
  props: {
    msg: String,
  },
  components: {},
  data() {
    return {
      title: "",
      todos: [],
    };
  },
  methods: {
    add() {
      if (this.title != "") {
        this.todos.push(this.title);
        this.title = "";
      }
    },
    remove(id) {
      // this.todos.splice(id, 1);
      this.$delete(this.todos, id);
    },
  },
};
</script>

